@tailwind base;
@tailwind components;
@tailwind utilities;

html{
  color-scheme:light;
}
::-webkit-scrollbar {
  width: 4px; /* Width of the vertical scrollbar */
  height: 6px; /* Height of the horizontal scrollbar */
  background-color: #2e3452;
  border: 2px solid #fff;
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: #bfa980; 
  border-radius: 10px;
  height: 4px; /* Minimum height for the scrollbar thumb */
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px grey;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #bfa980; 
}

html {
  scroll-behavior: smooth;
}

.main-input{
  border:2px solid #bfa980;
  color:"#fff";
  background-color: #1e2235;
}



.textShadow {
  text-shadow: 6px 6px 6px rgba(17, 17, 17, 0.5); /* X-offset, Y-offset, blur-radius, color */
}


.boxShadow{
  box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.5);;
}

.filterShadow{
  filter: drop-shadow(16px 16px 20px rgb(170, 10, 10));
}

.imageShadow::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -2px;
  background: linear-gradient(to bottom, transparent, #222222b7);
 
}

.displayHidden{
 visibility: hidden;
}
.accentOrange{
  accent-color: #bfa980;
  color: white;
}
.shad{
  box-shadow: 3px 3px 3px 2px rgba(5, 71, 3, 0.144);

}
.noshadow{
  text-shadow: 0px;
}

.customeHeight {
  height: calc(100vh - 35vh);
}


#modal-title{
  margin-right: 16px;
  margin-left: 16px;
  margin-top: 16px;
}


.comment-text {
  font-family: 'Arial', sans-serif;
  font-size: 16px;
  color: #333;
  line-height: 1.5;
  word-wrap: break-word;
