/* Container for category select box */
.category-select-container {
    display: flex;
    justify-content: center;
    margin: 20px;
  }
  
  .category-select {
    padding: 10px;
    border: 1px solid #bfa980;
    border-radius: 4px;
    font-size: 16px;
    /* color: #bfa980; */
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  

  
  /* Card animations */
  .product-card {
    opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 0.5s ease forwards;
  }
  
  @keyframes fadeInUp {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  