/* burger.module.css */
.burger-container {
  width: 30px;
  height: 20px;
  position: relative;
  cursor: pointer;
}

.burger-bar {
  width: 100%;
  height: 4px;
  background-color: #bfa980;
  position: absolute;
  transition: transform 0.3s, opacity 0.3s;

}

.burger-bar.middle {
  top: 50%;
  transform: translateY(-50%);
}

.burger-bar.top {
  top: 0;
}

.burger-bar.bottom {
  bottom: 0;
}

/* Adjusted styles for active state */
.burger-container.active .burger-bar.middle {
  opacity: 0;
}

.burger-container.active .burger-bar.top {
  transform: rotate(45deg) translate(45%, 0%);
}

.burger-container.active .burger-bar.bottom {
  transform: rotate(-45deg) translate(40%, 50%);
}
